import "./CSS/FileList.css"
import React, { useEffect, useState } from 'react'
import { FetchFile } from './Helper';
import Gallery from 'react-grid-gallery';
import pdf from "../Assets/pdf-icon.png"
import { render } from "@testing-library/react";
import { Button } from "@mui/material";
import DownloadIcon from '@mui/icons-material/Download';
const FileList = () => {
    const [customer, setCustomer] = useState({
        name: 'Kunal',
        id: '4513699',
        vehicle: {
            id: '961543',
            name: 'Honda City'
        }
    })
    const [Images, setImages] = useState([]);
    const [current, setCurrent] = useState();
    const [rerender, setRerender] = useState(true);
    setInterval(() => {
        setRerender(!rerender)
    }, 60000)
    useEffect(() => {
        FetchFile(customer, "RTAEVJCBKOIAUC").then((data) => {
            if (data.success) {
                setImages(data.links.map((file, index) => {
                    var ext = data.details[index]?.Key.toString().split("/")[2].split(".")[1]
                    return {
                        src: ext === "pdf" ? pdf : file,
                        thumbnail: ext === "pdf" ? pdf : file,
                        thumbnailWidth: 550,
                        thumbnailHeight: 450,
                        caption: data.details[index]?.Key.toString().split("/")[2],
                        thumbnailCaption: data.details[index]?.Key.toString().split("/")[2]
                    }
                }))
            } else {
                alert('Failed to load files')
            }
        }).catch(err => console.log(err))
    }, [render])
    const imagesStateChange = (index) => {
        setCurrent(index)
    }
    return (
        <div className="p-3">
            <h3 style={{ color: '#69A628' }} className="ps-6">Welcome {customer.name}!</h3>
            <div className="p-5">
                <div style={{
                    display: "block",
                    minHeight: "1px",
                    width: "100%",
                    border: "1px solid #ddd",
                    overflow: "auto"
                }}>
                    <Gallery images={Images}
                        currentImageWillChange={imagesStateChange}
                        showLightboxThumbnails={true}
                        customControls={[
                            <form method="get" action={Images[current]?.src}>
                                <Button variant="contained" endIcon={<DownloadIcon />} type="submit">Download!</Button>
                            </form>
                        ]}
                    />
                </div>
            </div>
        </div>
    )
}

export default FileList