import axios from "axios";

export const UploadFile = (files, customer, token) => {
    return (
        axios.post(`https://i49rzsm1y2.execute-api.ap-south-1.amazonaws.com/dev/file-upload/${customer.id}/${customer.vehicle.id}`, files, {
            
        }).then((res) => {
            return res.data;
        }).catch((err) => {
            console.log(err);
        })
    )
}

export const FetchFile = (customer, token) => {
    return (
        axios.get(`https://i49rzsm1y2.execute-api.ap-south-1.amazonaws.com/dev/get-all-files/${customer.id}/${customer.vehicle.id}`, {
            
        }).then((res) => {
            return res.data;
        }).catch((err) => {
            console.log(err);
        })
    )
}