import React, { useEffect, useState } from 'react';
import { useDropzone } from 'react-dropzone';
import styled from '@emotion/styled';
import { Box, Button, CircularProgress, Stack } from '@mui/material';
import SendIcon from '@mui/icons-material/Send';
import FolderSharedIcon from '@mui/icons-material/FolderShared';
import { UploadFile } from './Helper';
import { useNavigate } from "react-router-dom";
import Modal from '@mui/material/Modal';
const getColor = (props) => {
  if (props.isDragAccept) {
    return '#00e676';
  }
  if (props.isDragReject) {
    return '#ff1744';
  }
  if (props.isFocused) {
    return '#2196f3';
  }
  return '#eeeeee';
}
const Container = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  border-width: 2px;
  border-radius: 2px;
  border-color: ${props => getColor(props)};
  border-style: dashed;
  background-color: #fafafa;
  color: #bdbdbd;
  outline: none;
  transition: border .24s ease-in-out;
`;
function DropzoneTest() {
  const [token, setToken] = useState('');
  const [toggle, setToggle] = useState(false)
  const [customer, setCustomer] = useState({
    name: 'Kunal',
    id: '4513699',
    vehicle: {
      id: '961543',
      name: 'Honda City'
    }
  })
  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    textAlign: 'center',
    borderRadius: '10px',
    p: 4,
  };
  const {
    acceptedFiles,
    fileRejections,
    getRootProps,
    getInputProps,
    isFocused,
    isDragAccept,
    isDragReject,
  } = useDropzone({
    accept: {
      'image/jpg': ['.jpg'],
      'image/png': ['.png'],
      'image/tiff': ['.tif'],
      'application/pdf': ['.pdf']
    }
  });
  const navigate = useNavigate();
  const acceptedFileItems = acceptedFiles.map(file => (
    <li key={file.path}>
      {file.path} - {file.size} bytes
    </li>
  ));

  const fileRejectionItems = fileRejections.map(({ file, errors }) => (
    <li key={file.path}>
      {file.path} - {file.size} bytes
      <ul>
        {errors.map(e => (
          <li key={e.code}>{e.message}</li>
        ))}
      </ul>
    </li>
  ));
  const upload = (e) => {
    e.preventDefault();
    setToggle(true);
    var formData = new FormData();
    acceptedFiles.map((item, index) => {
      formData.append(item.name, item)
      if (index === acceptedFiles.length - 1) {
        UploadFile(formData, customer, token).then((data) => {
          if (data.success) {
            setToggle(false);
            alert("Files uploded successfully")
          }
        }).catch((err) => {
          setToggle(false);
          console.log(err)
        })
      }
    })
  }
  useEffect(() => {
    if (acceptedFiles.length !== 0) {
      console.log(acceptedFiles);
    }
  }, [acceptedFiles])
  const redirect = (e) => {
    e.preventDefault();
    navigate("/files")
  }
  return (
    <div className="container p-5">
      <div className="row mb-2">
        <div className="col">
          <Button className="float-end" variant="contained" onClick={e => redirect(e)} endIcon={<FolderSharedIcon />}>Vehicle Files</Button>
        </div>
      </div>
      <Modal
        open={toggle}
        onClose={() => setToggle(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <CircularProgress color="success" />
          <h4 className="test-secondary">Uploading</h4>
        </Box>
      </Modal>

      <form encType='multipart/form-data'>
        <Container {...getRootProps({ isFocused, isDragAccept, isDragReject })}>
          <input {...getInputProps()} />
          <p>Drag 'n' drop some files here, or click to select files</p>
        </Container>
        <aside>
          {acceptedFileItems.length > 0 ?
            <div className="pt-4">
              <h6 className="text-success">Accepted files</h6>
              <ul>{acceptedFileItems}</ul>
            </div>
            : ''}
          {fileRejectionItems.length > 0 ?
            <div className="pt-4">
              <h6 className="text-danger">Rejected files</h6>
              <ul>{fileRejectionItems}</ul>
            </div>
            : ''}
        </aside>
        <div className="mb-3 pt-4">
          <label htmlFor="jwt" className="form-label">JWT Token</label>
          <textarea onChange={e => setToken(e.target.value)} className="form-control" id="jwt" rows="3"></textarea>
        </div>
        <div className="pt-2">
          <Stack direction="row" spacing={2}>
            <Button onClick={upload} variant="contained" endIcon={<SendIcon />} disabled={acceptedFileItems.length === 0}>
              Upload
            </Button>
          </Stack>
        </div>
      </form>
    </div>
  );
}
export default DropzoneTest